<template>
  <div name="InfoMain" class="InfoMain-wrap">
    <el-row>
      <div style="display: flex; margin: 20px 0 20px 100px">
        <div class="titleline"></div>
        <div style="margin: 10px 0 0 10px">
          <font class="title">个人资料信息设置</font>
        </div>
      </div>
    </el-row>

    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <div class="forms">
        <el-row type="flex" justify="space-between" style="width: 620px">
          <font>用户名称：</font>
          <el-form-item prop="user_name">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.user_name"
              placeholder="请设置您的名称"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 620px">
          <font>用户所学专业：</font>
          <el-cascader
            style="width: 400px; height: 50px"
            v-model="ruleForm.user_major"
            placeholder="请选择您目前所在的/擅长的专业类别"
            :options="options"
            :props="{ expandTrigger: 'hover' }"
            :value="value"
          ></el-cascader>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 620px">
          <font>所在院校：</font>
          <el-form-item prop="user_college">
            <el-input
              style="width: 400px"
              class="public-uncheck"
              placeholder="请设置您所在的学校"
              v-model="ruleForm.user_college"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 620px">
          <font>用户签名：</font>
          <el-form-item prop="user_sign">
            <el-input
              style="width: 400px"
              class="public-uncheck"
              placeholder="请设置您的个性签名"
              v-model="ruleForm.user_sign"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 620px">
          <font>用户个人简介：</font>
          <el-form-item prop="user_introduce">
            <el-input
              style="width: 400px"
              class="public-uncheck"
              placeholder="请设置您的个人简介(有关于自身能力的介绍)"
              v-model="ruleForm.user_introduce"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 620px">
          <font>个人头像：</font>
          <el-upload
            class="avatar-uploader"
            action="https://www.mocky.io/v2/5185415ba171ea3a00704eed/posts/"
            :show-file-list="false"
            :on-success="headhandleAvatarSuccess"
            :before-upload="headbeforeAvatarUpload"
          >
            <img v-if="headimageUrl" :src="headimageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 620px">
          <font>背景图片：</font>
          <el-upload
            class="avatar-uploader"
            action="https://www.mocky.io/v2/5185415ba171ea3a00704eed/posts/"
            :show-file-list="false"
            :on-success="backhandleAvatarSuccess"
            :before-upload="backbeforeAvatarUpload"
          >
            <img v-if="backimageUrl" :src="backimageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-row>

        <el-row type="flex" justify="end" style="margin: 30px 60px 0 0">
          <div class="submit" @click="submit()">
            <font>提&emsp;交</font>
          </div>
        </el-row>
      </div>
    </el-form>
  </div>
</template>

<script>
import { changeUserInfo } from "@/api/homeAPI/articleslist";
