  name: "InfoMain",
  data() {
    return {
      headimageUrl: "",
      headimageFile: {},
      backimageUrl: "",
      backimageFile: {},
      value: [],
      options: [
        {
          value: "文学",
          label: "文学",
          children: [
            {
              value: "中国语言文学类",
              label: "中国语言文学类",
            },
            {
              value: "新闻传播学类",
              label: "新闻传播学类",
            },
            {
              value: "外国语言文学类",
              label: "外国语言文学类",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "艺术学",
          label: "艺术学",
          children: [
            {
              value: "音乐",
              label: "音乐",
            },
            {
              value: "舞蹈",
              label: "舞蹈",
            },
            {
              value: "影视学类",
              label: "影视学类",
