              label: "设计学类",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "理学",
          label: "理学",
          children: [
            {
              value: "信息与计算科学",
              label: "信息与计算科学",
            },
            {
              value: "数学与应用数学",
              label: "数学与应用数学",
            },
            {
              value: "应用化学",
              label: "应用化学",
            },
            {
              value: "生物技术",
              label: "生物技术",
            },
            {
              value: "生物科学",
              label: "生物科学",
            },
            {
              value: "应用物理学",
              label: "应用物理学",
            },
            {
              value: "应用心理学",
              label: "应用心理学",
            },
            {
              value: "统计学",
              label: "统计学",
            },
            {
              value: "化学",
              label: "化学",
            },
            {
              value: "物理学",
              label: "物理学",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "工学",
          label: "工学",
          children: [
            {
              value: "土木工程",
              label: "土木工程",
            },
            {
              value: "机械类专业",
              label: "机械类专业",
            },
            {
              value: "电气工程及其智能化",
              label: "电气工程及其智能化",
            },
            {
              value: "计算机科学与技术",
              label: "计算机科学与技术",
            },
            {
              value: "车辆工程",
              label: "车辆工程",
            },
            {
              value: "软件工程",
              label: "软件工程",
            },
            {
              value: "交通运输工程",
              label: "交通运输工程",
            },
            {
              value: "自动化",
              label: "自动化",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "农学",
          label: "农学",
          children: [
            {
              value: "园艺",
              label: "园艺",
            },
            {
              value: "种子科学与工程",
              label: "种子科学与工程",
            },
            {
              value: "设施农业科学与工程",
              label: "设施农业科学与工程",
            },
            {
              value: "农业资源与环境",
              label: "农业资源与环境",
            },
            {
              value: "动物科学",
              label: "动物科学",
            },
            {
              value: "林学",
              label: "林学",
            },
            {
              value: "水产养殖学",
              label: "水产养殖学",
            },
            {
              value: "草业学科",
              label: "草业学科",
            },
            {
              value: "茶学",
              label: "茶学",
            },
            {
              value: "烟草",
              label: "烟草",
            },
            {
              value: "农艺教育",
              label: "农艺教育",
            },
            {
              value: "园艺教育",
              label: "园艺教育",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "其他学科",
          label: "其他学科",
        },
      ],
      ruleForm: {
        user_id: "",
        user_name: "用户名称",
        user_head: "https://dummyimage.com/400X400", //默认头像
        user_background: "https://dummyimage.com/400X400", //默认背景
        user_major: "未知",
        user_college: "未知",
        user_sign: "请多多指教~！",
        user_introduce: "该用户暂未设置个人简介哦~",
      },
      rules: {
        user_name: [
          { required: true, message: "用户名是必填项！", trigger: "blur" },
        ],
        user_major: [
          { required: true, message: "专业设置是必填项！", trigger: "blur" },
        ],
        user_college: [
          { required: true, message: "学校设置是必填项！", trigger: "blur" },
        ],
        user_introduce: [
          { required: true, message: "个人简介是必填项！", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    headhandleAvatarSuccess(res, file) {
      console.log(res);
      this.headimageUrl = URL.createObjectURL(file.raw);
    },
    headbeforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      if (isJPG && isLt2M) {
        this.headimageFile = file;
        console.log("头像文件！", this.headimageFile);
      }
    },
    backhandleAvatarSuccess(res, file) {
      console.log(res);
      this.headimageUrl = URL.createObjectURL(file.raw);
    },
    backbeforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      if (isJPG && isLt2M) {
        this.backimageFile = file;
        console.log("背景文件！", this.backimageFile);
      }
    },
    submit() {
      let headreader = new FileReader();
      headreader.readAsDataURL(this.headimageFile);
      let _this = this;
      headreader.onload = function () {
        _this.ruleForm.user_head = this.result;
      };
      let backreader = new FileReader();
      backreader.readAsDataURL(this.backimageFile);
      backreader.onload = function () {
        _this.ruleForm.user_background = this.result;
      };
      setTimeout(() => {
        changeUserInfo(_this.ruleForm).then((res) => {
          console.log(res.data);
        });
      }, 1000);
    },
  },
  mounted() {
    if (localStorage.getItem("user_info")) {
      this.ruleForm.user_id = JSON.parse(localStorage.getItem("user_info")).id;
    }
  },
};
</script>

<style scoped>
.InfoMain-wrap {
  position: relative;
  width: 100%;
  background: #ffffff;
  border-radius: 10px;
  padding: 20px;
}
.titleline {
  width: 10px;
  height: 50px;
  background-color: #ff5900;
}
.title {
  font-family: SourceHanSansCN-Bold;
  font-size: 24px;
  font-weight: bold;
  color: #3d3d3d;
}
.forms {
  margin: 0 0 0 150px;
}
.forms > * {
  margin: 20px 0 20px 0;
}
.forms > * font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  color: #3d3d3d;
}
.submit {
  width: 150px;
  height: 60px;
  border-radius: 30px;
  background: #76fff5;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s;
}
.submit font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  line-height: 18px;
  color: #3d3d3d;
  transition: all 0.5s;
}
.submit:hover {
  background-color: #00ead8;
}
.submit:hover font {
  color: #ffffff;
}
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>